import Vue from "vue";
import Vuex from "vuex";
import { persistentStore, getInitialState } from "./persistData";
import appConfigStoreModule from '@core/@app-config/appConfigStoreModule'
import app from './app';

Vue.use(Vuex);

export default new Vuex.Store({
    state: getInitialState(),
    plugins: [persistentStore],
    modules: {
        appConfig: appConfigStoreModule,
        app
    },
    getters: {
        getSelectedInstrument(state) {
            return state.selectedInstrument;
        },
        selectedDuration(state) {
            return state.selectedDuration;
        },
        network(state) {
            return state.network;
        },
        account(state) {
            return state.account;
        },
        loading(state) {
            return state.loading;
        },
        balance(state) {
            return state.balance;
        },
        errorMessage(state) {
            return state.errorMessage;
        },
        durations(state) {
            return state.durations
        }
    },
    mutations: {
        setSelectedInstrument(state, selectedInstrument) {
            state.selectedInstrument = selectedInstrument;
        },
        setNetwork(state, network) {
            state.network = network;
        },
        setAccount(state, account) {
            state.account = account
        },
        startLoading(state) {
            state.loading = true
        },
        stopLoading(state) {
            state.loading = false
        },
        setBalance(state, balance) {
            state.balance = balance;
        },
        setSelectedDuration(state, duration) {
            state.selectedDuration = duration
        },
        setErrorMessage(state, e) {
            if(!e) {
                state.errorMessage = null
                return
            }
            console.error(e);
            if(e.error && e.error.body) {
                try {
                    let error = JSON.parse(e.error.body);
                    if(error.error && error.error.message) {
                        state.errorMessage = error.error.message
                        return
                    }
                } catch (err) {
                    console.log(err)
                }
            }
            if(e.data && e.data.message) {
                state.errorMessage = e.data.message;
                return
            }
            if(e.message) {
                if(e.message.data && e.message.data.message) {
                    state.errorMessage = e.message.data.message
                    return
                }
                state.errorMessage = e.message
                return
            }
            state.errorMessage = "Oops! Something went wrong. Please try again later."
        }
    }
});
