const persistInterval = 5*24*60*60*1000; // 5 days

const persistentStore = store => {
    store.subscribe( ( mutation, state ) => {
        let record = { data:  state, ts: new Date().getTime() + persistInterval };
        try {
            //record.data.errorMessage = null;
            localStorage.setItem( 'storeData', btoa(JSON.stringify( record )));
        } catch(e) {
            console.log(e);
        }
    });
};

const getInitialState = (reset = false) => {
    let state = {
        loading: false,
        selectedInstrument: null,
        network: null,
        account: null,
        balance: "0",
        errorMessage: null,
        selectedDuration: null,
        durations: [{
            label: "15 Minutes",
            duration: 15*60
        },{
            label: "1 Hour",
            duration: 60 * 60
        },{
            label: "1 Day",
            duration: 24 * 60 * 60
        },{
            label: "1 Week",
            duration: 7 * 24 * 60 * 60
        },{
            label: "1 Month",
            duration: 30 * 24 * 60 * 60
        }]
    };
    if(reset) {
        return state
    }
    let storedState = null;
    try {
        storedState = localStorage.getItem("storeData");
        if (storedState) {
            storedState = JSON.parse(atob(storedState));
        }
    } catch (e) {
        console.log(e);
    }
    if( storedState && new Date().getTime() < storedState.ts ) {
        storedState.data.loading = false;
        storedState.data.errorMessage = null;
        return {
            ...state,
            ...storedState.data
        }
    }
    return state;
}

export {
    persistentStore,
    getInitialState
};
